const kelas_5 = {
  async render() {
    return `
      <main>
      <h1 class="class_name">Presentasi Bisnis - Menyajikan dengan Percaya Diri</h1><br>
      <br><img src="class/presentasi bisnis.jpg" alt=""><br>
  
      <br><h2 class="sub_class">
          Langkah-langkah Presentasi Bisnis
      </h2><br>
      <p class="desc_class">
          Apakah Anda pernah melakukan presentasi di depan orang banyak? Apa hal yang Anda khawatirkan ketika melakukan presentasi? Apakah kekurangan yang Anda sadari saat melakukan presentasi? Jika jawabannya iya coba lah untuk menyimak video berikut yang berujudul <a href="https://www.youtube.com/watch?v=5M0i6Fbe-q8">5 Menit Yang Mengubah Hidup Selamanya</a>. Untuk dapat menyampaikan presentasi dengan efektif dan efisien, berikut ini merupakan tahapan dalam melakukan presentasi:
          <ol>
              <li>Preparation</li>
              <li>Introduction</li>
              <li>Main Points</li>
              <li>Conclusion</li>
          </ol> 
  
      <br><h2 class="sub_class">
          Preparation
      </h2><br>
      <div class="desc_class">
          <ul>Sebelum melakukan presentasi, pastikan Anda telah mengetahui hal-hal berikut:
              <li>Siapa audience/peserta yang akan hadir?</li>
              <li>Apa yang mereka perlukan?</li>
              <li>Apa yang telah mereka ketahui?</li>
              <li>Apa kesamaan yang dimiliki?</li>
          </ul>
          <br><ul>Agar presentasi kita optimal dan menarik, kita juga perlu memerhatikan dan mempersiapkan hal-hal berikut ini:
              <li>Persiapkan outfit yang akan digunakan.</li>
              <li>Persiapkan ice breaking atau jokes.</li>
              <li>Persiapkan jawaban atas pertanyaan-pertanyaan yang mungkin akan muncul.</li>
          </ul> 
      </div>
  
      <br><h2 class="sub_class">
          Introduction
      </h2><br>
      <div class="desc_class">
          <ul>Pada tahap introduction (pengenalan), pastikan Anda menyampaikan hal-hal berikut:
              <li>Jelaskan siapa Anda</li>
              <li>Jelaskan alasan kehadiran Anda</li>
              <li>Sampaikan apa yang akan Anda bicarakan</li>
              <li>Jelaskan bagaimana Anda akan menyampaikannya</li>
          </ul>
          <br><p>
              berikut adalah kata-kata pada saat introduction. “Selamat siang Rekan-Rekan semuanya, terima kasih telah bergabung pada
              acara siang hari ini.” (Salam dan Sapa). “Saya Ariel, Tim Develop Website 1%, saya akan menjelaskan tentang materi berikut ini” (Perkenalan). “Selama 30 menit kedepan saya akan mempresentasikan materi beriktu, yang dibagi menjadi 3 bagian, yaitu Preparation, Introduction, Main Points, dan Conclusion” (Tujuan & Garis Besar (Outline) Presentasi). “Jika ada pertanyaan, jangan segan bertanya pada akhir sesi nanti.” (Informasi untuk
              Q&A). Bagaimana jika kita menjadi perwakilan yang harus memperkenalkan tim sebelum presentasi? untuk perkenalannya seperti ini, “Saya Ariel, saya bekerja di 1% Team sebagai Developer. Saya disini bersama tim saya, perkenalkan Zhaky, dan Adit, dan juga Darmawan kami akan menjelaskan tentang proses pembuatan website ini“. ada beberapa tips dalam melakukan presentasi bisnis bagian introduction, yaitu: 
          </p>
          <ul>
              <li>Proper greetings atau Salam yang benar </li>
              <li>Grab attention form audiences atau Tarik perhatian dari audiens</li>
              <li>Inform the duration atau Informasikan durasinya</li>
          </ul>
      </div>
  
      <br><h2 class="sub_class">
          Main Points
      </h2>
      <div class="desc_class">
          <ul>Pada tahap main points, pastikan Anda menyampaikan hal-hal berikut:
              <li>Menjelaskan inti dari
                  presentasi yang akan
                  disampaikan (overview)</li>
              <li>Menyampaikan key
                  points presentasi</li>
              <li>Setiap key points
                  dijelaskan dari makro
                  ke mikro.</li>
              <li>Upayakan memberikan
                  contoh yang familiar
                  untuk audiens.</li>
              <li>Jika pindah ke key points
                  selanjutnya, pastikan
                  berkesinambungan antar
                  point-nya.</li>
              <li>Beri jeda/break jika
                  diperlukan.</li>
          </ul>
          <p>
              Untuk memudahkan audiens memahami penjelasan yang disampaikan, berikut hal-hal yang bisa dilakukan. yang pertama gunakan visual yang baik dan menarik, tidak berlebihan. Kedua Sampaikan data / trend / quote untuk memperkuat fakta. Dan yang ketiga atau terakhir Berikan highlight / emphasis untuk setiap hal yang penting.
          </p>
      </div>
      
      <br><h2 class="sub_class">
          Conclusion
      </h2><br>
      <div class="desc_class">
          <ul>Pada tahap conclusion (kesimpulan), pastikan Anda menyampaikan hal-hal berikut:
              <li>Sampaikan/ulangi inti
                  pesan Anda menjadi
                  sebuah kesimpulan
                  yang ringkas.</li>
              <li>Sampaikan harapan
                  yang ingin dicapai
                  peserta/audience
                  setelah acara berakhir.</li>
              <li>Salam
                  penutup.</li>
          </ul>
  
      </div>
  
      <br><h2 class="sub_class">
          Presentasi Bisnis Petunjuk
      </h2><br>
      <div class="desc_class">
          <ol>Bagaimana sih menjawab pertanyaan yang susah saat melakukan presentasi?
              <li>Konfirmasi pertanyaan yang diberikan.</li>
              <li>Berikan informasi atau jawaban yang Anda diketahui.</li>
              <li>Akui jika Anda belum mengetahui jawaban lengkapnya.</li>
              <li>Informasikan bahwa Anda akan memberikan jawaban lengkap dan komprehensif saat Anda sudah mencari tahu.</li>
              <li>Tanyakan kepada yang bertanya cara menghubunginya, jika Anda belum mengetahuinya.</li>
          </ol>
          <br>
          <ul>Tips Visualisasi Materi Presentasi
              <li>Buatlah atau gunakan template slides dengan warna yang sesuai dengan brand/produk perusahaan.</li>
              <li>Hindari penggunaan warna yang terlalu banyak.</li>
              <li>Gunakan font dan ukuran font yang tepat dan konsisten.</li>
              <li>Hindari penggunaan text yang wordy/terlalu panjang.</li>
              <li>Beri highlight untuk kata kunci yang penting pada text.</li>
              <li>Hindari tumpang tindih informasi dalam satu slide.</li>
              <li>Tampilkan data dengan visual yang mudah dipahami (flowchart/diagram).</li>
              <li>Gunakan gambar atau video berkualitas baik.</li>
              <li>Hindari terlalu banyak efek khusus.</li>
              <li>Batasi jumlah slide dan sesuaikan dengan durasi presentasi.</li>
          </ul>
      </div>
      <br><h2 class="sub_class">
          The Do’s and The Don’ts
      </h2><br>
      <div class="desc_class">
          <ul>Do’s
              <li>Gunakan bahasa dan istilah-istilah yang mudah dipahami atau sesuaikan dengan audiens.</li>
              <li>Perhatikan suara dan intonasi.</li>
              <li>Pastikan melakukan eye-contact dengan audiens.</li>
              <li>Gunakan bahasa tubuh/gesture yang baik.</li>
              <li>Libatkan atau ajak audiens untuk berinteraksi.</li>
              <li>Jangan lupa tersenyum.</li>
          </ul>
          <br>
          <ul>Don’ts
              <li>Hindari penjelasan yang berbelit-belit.</li>
              <li>Jangan terlalu fokus dengan slide presentasi.</li>
              <li>Hindari slide yang terlalu panjang dan monoton.</li>
              <li>Jangan berbicara terlalu cepat atau terlalu lambat.</li>
              <li>Jangan terlalu sering menggunakan suara dan animasi.</li>
              <li>Jangan gunakan font yang sulit dibaca atau tidak biasa</li>
          </ul>
      </div>
  
      <br><br><p class="motivasi"><i>"A great presentation gives smart ideas an advantage"</i></p>
      </main>
          `;
  },

  async afterRender() {
    // Fungsi ini akan dipanggil setelah render()
  },
};

export default kelas_5;
