const kelas_4 = {
  async render() {
    return `
      <main>
      <h1 class="class_name">Communication</h1><br>

  <div class="sub_class">
      <h2>
          Apa itu Komunikasi
      </h2>
      <img src="class/kelas4_1.jpg" class="image-class" alt="">
      <p>
          Berdasarkan Kamus Besar Bahasa Indonesia (KBBI), komunikasi adalah pengiriman dan penerimaan pesan atau berita antara dua orang atau lebih sehingga pesan yang dimaksud dapat dipahami.Komunikasi yang efektif itulah yang membuat pesan yang dimaksud mudah dipahami.
      </p>
      <p>Komunikasi yang efektif menjadi salah satu kunci keberhasilan dalam menjalin hubungan, tidak hanya saat bersama pasangan namun juga dengan rekan kerja atau keluarga.</p>

  </div>
  <div class="subclass">
      <h2>7C dalam komunikasi</h2>
      <ol>
          <li>
              <b>Concise</b>
          </li>
          <p>Concise atau ringkas adalah salah satu dari 7C dalam komunikasi yang perlu kamu ketahui. Melansir World of Work, pesan yang ringkas akan lebih mudah diingat penerima pesan.</p>
          <p>Sehingga, pastikan kamu tidak menyampaikan pesan secara bertele-tele. Ketika kamu terlalu banyak memberi detail dalam pesanmu, penerima pesan akan kesulitan untuk memahami dan mengingatnya.</p>
          <p>Supaya pesanmu tetap ringkas, ingatlah beberapa hal berikut:</p>
          <p><ul>
              <li>asumsikan bahwa materi pesan yang akan disampaikan merupakan hal baru bagi penerimanya</li>
              <li>sampaikan pesan dengan tidak bertele-tele</li>
              <li>pastikan tidak ada informasi penting yang terlewat dalam pesan yang disampaikan</li>
              <li>edit isi pesan supaya poin utama dijelaskan seakurat mungkin</li>
              <li>hilangkan hal-hal yang tidak berkaitan dengan poin utama yang ingin disampaikan</li>
          </ul></p>
          <li>
              <b>Complete</b>
          </li>
          <p>Komponen 7C dalam komunikasi lainnya adalah complete. Maksudnya, pastikan informasi disampaikan seluruhnya tanpa ada detail penting yang terlewat.</p>
          <p>Hal ini penting terutama jika kamu berdiskusi seputar topik yang kompleks atau memiliki istilah ilmiah yang umum terdapat dalam konteks pendidikan, hukum, atau bisnis.</p>
          <p>Adapun komunikasi yang jelas dan komplit adalah pesan yang dipahami audiens sesuai dengan maksudmu. Sehingga, ketika informasi yang akan disampaikan berisiko menimbulkan miskomunikasi, pastikan untuk menyampaikannya selengkap mungkin ketimbang terpaksa meringkasnya.</p>
          <li>
              <b>Coherent</b>
          </li>
          <p>Komponen lain dari 7C dalam komunikasi adalah coherent. Hal ini sangat terkait dengan struktur dari komunikasi. Sehingga, cobalah untuk mengorganisir pesan yang akan disampaikan supaya alur informasi di dalamnya mudah dibaca dan diingat.</p>
          <p>Tidak hanya itu, coherent juga berarti memahami audiensmu dan menyesuaikan pesanmu dengan mereka. Sebagai contoh, kamu dapat menggunakan kosakata yang mudah dimengerti ketika berbicara dengan karyawan baru. Namun, kamu bisa menggunakan terminologi yang khusus dan teknikal ketika memberikan presentasi akademis.</p>
          <li>
              <b>Clear</b>
          </li>
          <p>Bagian lain dari 7C dalam komunikasi adalah clear atau kejelasan. Hal tersebut berarti memastikan pesanmu bebas dari kosakata dan terminologi yang tidak penting dan membingungkan.</p>
          <p>Menurut Indeed, beberapa cara yang bisa kamu lakukan untuk meningkatkan kejelasan dari pesanmu adalah sebagai berikut.</p>
          <p>
              <ul>
                  <li>membatasi atau menghilangkan penggunaan idiom</li>
                  <li>tidak menggunakan istilah ilmiah, jargon, atau frase yang spesifik dan berkaitan ke suatu bidang profesional kecuali jika cocok dengan audiens</li>
                  <li>gunakan kalimat pembuka ketika harus menggunakan istilah ilmiah, seperti “profesional di bidang marketing biasanya menyebut konsep ini sebagai…”</li>
                  <li>jangan menggunakan bahasa slang, meskipun hal tersebut dapat diterima oleh audiensmu</li>
                  <li>gunakan kalimat aktif dan sederhana</li>
              </ul>
          </p>
          <li>
              <b>Clear</b>
          </li>
          <p>Secara singkat, maksud dari courteous sebagai bagian dari 7C dalam komunikasi adalah sikap yang baik atau sopan. Salah satu bentuk dari menunjukkan kesopanan dalam berkomunikasi adalah dengan menjaga kontak mata dengan audiens. Tidak hanya itu, kamu juga berkomunikasi dengan audiensmu menggunakan gaya percakapan.</p>
          <p>Hal tersebut karena gaya percakapan membantu audiens untuk mendengar secara aktif ketika kamu berkomunikasi dengan mereka. Beberapa hal lain yang perlu dipertimbangkan supaya komunikasimu menunjukkan kesopanan adalah sebagai berikut:</p>
          <p>
              <ul>
                  <li>berbicara ke audiens dengan rasa menghargai</li>
                  <li>tidak membuat asumsi terhadap audiens</li>
                  <li>menunjukkan apresiasi untuk perhatian dan waktu dari audiens</li>
              </ul>
          </p>
          <li>
              <b>Concrete</b>
          </li>
          <p>Bagian lain dari 7C dalam komunikasi adalah concrete. Maksudnya, pastikan bahwa logika dan pesan yang disampaikan saling mendukung satu sama lain. Tidak hanya itu, ketika berkomunikasi pun kamu menyampaikan pesan atau argumen berdasarkan fakta dari sumber kredibel.</p>
          <p>Concrete juga berarti menggunakan kata-kata yang mudah dimengerti dan sederhana. Sehingga, kamu tidak akan banyak menggunakan kata sifat dan kata keterangan.</p>
          <p>Sebagai contoh, kamu akan menggunakan kata “sprint” ketimbang “berlari dengan cepat”. Semakin sederhana kata yang digunakan, penyampaian pesan pun akan menjadi lebih efektif.</p>
          <li>
              <b>Correct</b>
          </li>
          <p>Komponen terakhir dari 7C dalam komunikasi adalah correct. Maksudnya, ketika berkomunikasi, pastikan fakta dan gramatikal sudah benar.</p>
          <p>Hal tersebut memastikan audiens menerima informasi darimu dengan baik. Supaya komunikasimu dapat memenuhi salah satu komponen 7C ini, berikut adalah beberapa hal yang bisa dilakukan:</p>
          <p><ul>
              <li>ketika menulis, carilah proofreader, terutama dari orang yang memiliki pengelaman atau pendidikan di bidang komunikasi</li>
              <li>cek ulang materi yang telah kamu buat dan akan sampaikan dari awal sampai akhir</li>
              <li>saat menulis, pastikan untuk menyelesaikan keseluruhan draft terlebih dahulu sebelum mengeditnya, hal tersebut membantumu untuk mengetahui poin apa yang ingin kamu sampaikan</li>
          </ul></p>
      </ol>
  </div>
  <div class="subclass">
      <h2>Beberapa penyebab komunikasi yang tidak efektif</h2>
      <img src="class/kelas4_2.jpg" class="image-class" alt="">
      <ol>
          <li>
              <b>Tidak memerhatikan selama percakapan</b>
          </li>
          <p>Melansir Iidmglobal, penghambat komunikasi yang paling umum ditemukan di kantor adalah kurangnya perhatian pekerja selama percakapan berlangsung. Sebenarnya, hal ini terjadi tak hanya pada sebuah percakapan, bahkan saat meeting pun pekerja bisa tidak memerhatikan materi yang sedang disampaikan.</p>
          <p>Biasanya, hal ini terjadi karena pekerja teralihkan oleh notifikasi pada smartphone, hingga percakapan orang lain yang tak jarang terasa mengganggu.</p>
          <p>Oleh karena itu, langkah terbaik untuk mengantisipasi hal ini adalah ‘management of attention’. Berikut adalah tiga hal yang dapat membantu proses ‘management of attention’:</p>
          <p><ul>
              <li>Kurangi gangguan yang bisa dikelola: Contohnya adalah multitasking. Metode kerja seperti ini sebenarnya kurang efisien. Beralih dari satu aktivitas ke aktivitas berikutnya hanya akan memberikan ilusi efisiensi. </li>
              <li>Fokus pada satu percakapan dalam satu waktu: Jika percakapan penting, coba pusatkan perhatian pada percakapan tersebut agar fokusmu tidak terbagi.</li>
              <li>Cari tahu waktu terbaik saat fokusmu masih baik:  Percakapan penting sebaiknya dilakukan saat fokus masih baik. Coba tentukan waktu di mana fokus dan energimu masih segar.</li>
          </ul></p>
          <li>
              <b>Sikap dan keadaan emosional</b>
          </li>
          <p>Berikutnya, penghambat komunikasi di tempat kerja adalah keadaan emosional serta sikap seorang pekerja. Jika sedang berada di bawah tekanan masalah pribadi ataupun pekerjaan, pekerja akan terpengaruh keadaan emosional dan cenderung tidak dapat menerima pesna dengan baik.</p>
          <p>Kamu perlu mengambil jeda dan meninggalkan pekerjaan untuk sejenak. Dengan cara seperti ini, atasan dan rekan kerjamu bisa menyampaikan pesan di lain waktu.</p>
          <li>
              <b> Kemampuan komunikasi nonverbal yang kurang baik</b>
          </li>
          <p>Komunikasi nonverbal adalah salah satu elemen yang penting dalam dunia kerja. Maka, menurut Ready Training Online, kurangnya kemampuan komunikasi nonverbal dapat menjadi penghambat komunikasi di kantor.</p>
          <p>Apa yang tidak kamu katakan itu sama pentingnya dengan kata-kata yang kamu ucapkan. Pastikan bahasa tubuhmu dapat mengkomunikasikan pesan perhatian, keramahan, dan penerimaan.</p>
          <p>Pertahankan kontak mata saat sedang berbicara maupun mendengarkan. Lalu,  hindari gerakan seperti mengerutkan alis atau menyilangkan lengan. Penggunaan mimik muka dan bahasa tubuh yang salah juga dapat memberikan kesan yang kurang baik.</p>
          <li>
              <b>Budaya dan bahasa</b>
          </li>
          <p>Melansir Parallel Project Training, budaya dan bahasa adalah penghambat lain untuk komunikasi di kantor. Masing-masing budaya memiliki kebiasaan dan cara yang berbeda bila menyangkut perihal komunikasi. Bahasa juga bisa menjadi penghambat saat pekerja harus berhadapan dengan individu dari negara dengan bahasa yang berbeda.</p>
          <p>Oleh karena itu, para pekerja beserta atasan harus memastikan bahwa komunikasi disampaikan dalam bahasa yang sederhana untuk menghindari kesalahpahaman. Selain itu, pekerja sebaiknya menguasai bahasa Inggris sebagai bahasa internasional yang pada dasarnya sudah umum ditemukan dalam dunia kerja.</p>
          <li>
              <b>Kurangnya budaya timbal balik</b>
          </li>
          <p>Hal terakhir yang menjadi penghambat komunikasi di kantor adalah kurangnya budaya timbal balik. Biasanya, pekerja hanya akan mendapatkan timbal balik bila mereka mengajukannya kepada atasan.</p>
          <p>Di sisi lain, pekerja juga biasanya tidak mengajukan pertanyaan bila ada sebuah pernyataan yang kurang jelas. Nah, oleh karena itu, budaya timbal balik di kantor dibangun dan diperbaiki secara perlahan. Atasan perlu memberikan timbal balik. Begitu pula pekerja harus mengajukan pertanyaan bila pesan dirasa belum tersampaikan dengan baik.</p>
      </ol>
  </div>
  <br><br><p class="motivasi"><i>“The art of communication is the language of leadership.” <br>— James Humes</i></p>
  </main>
          `;
  },

  async afterRender() {
    // Fungsi ini akan dipanggil setelah render()
  },
};

export default kelas_4;
